.template3 {
  font-family: $yugo;
  color: $black;
  padding: 50px 0 0;
  @include sp {
    padding: 30px 0 0;
  }

  &__banner {
    width: 100%;
    padding: 0 2.24%;
    @include sp {
      padding: 0 3%;
    }
  }

  &__banner-container {
    position: relative;
    max-width: 1834.26px;
    width: 100%;
    height: 706px;
    border-radius: 60px;
    overflow: hidden;
    background: $primary;
    margin: 0 auto;
    @include sp {
      height: unset;
      border-radius: 30px;
    }
  }

  &__banner-wrap {
    height: 100%;
    position: relative;
    padding: 0 5.45%;
    @include mx1200 {
      padding: 0 3.5%;
    }
    @include sp {
      padding: 0 5.45%;
    }
  }

  &__banner-image {
    position: absolute;
    bottom: 100px;
    left: 0;
    padding: 0 5.45%;
    width: 100%;
    @include mx1200 {
      padding: 0 3.5%;
    }
    @include sp {
      bottom: 25px;
      padding: 0 5.45%;
    }
  }

  &__banner-title {
    color: $brown-91;
    font-size: 6.2rem;
    line-height: getem(62, 124);
    margin-bottom: 50px;
    @include sp {
      font-size: getsp375(6rem);
      margin-bottom: 25px;
      line-height: 1.5em;
    }
  }

  &__banner-subtitle {
    font-size: 1.8rem;
    line-height: getem(18, 36);
    font-weight: $medium;
    @include sp {
      width: 67%;
      font-size: getsp(28px);
    }
  }

  &__banner-text {
    padding: 100px 0 0 0;
    @include sp {
      padding: 30px 0 50px 0;
    }
  }

  &__action-content {
    @include sp {
      padding: 0 3%;
    }
  }

  &__action-content-wrap {
    width: 800px;
    height: 300px;
    box-shadow: 0px -2px 10px rgba($color: $black, $alpha: 0.05);
    margin: 150px auto;
    padding: 0 40px;
    border-radius: 60px;
    border: 3px solid $primary;
    position: relative;
    background-color: $white;
    flex-direction: column;
    @include center;
    @include sp {
      width: 100%;
      height: unset;
      padding: 20px 10px;
      margin: 75px auto;
      border-radius: 30px;
    }

    &::before {
      content: '';
      position: absolute;
      background-color: #F2EFEB;
      border-radius: 60px;
      width: 100%;
      height: 100%;
      z-index: -1;
      bottom: -12px;
      right: -12px;
      box-shadow: 0 5px 10px rgba($color: $black, $alpha: 0.2);
      @include sp {
        border-radius: 30px;
        bottom: -6px;
        right: -6px;
      }
    }
  }

  &__action-text-wrap {
    flex-direction: column;
    z-index: 1;
    margin-left: -160px;
    @include center;
    @include sp {
      margin: 0;
      text-align: center;
    }
  }

  &__action-subtitle {
    font-size: 2.6rem;
    font-weight: $medium;
    @include sp {
      font-size: getsp375(36px);
    }
  }

  &__action-title {
    font-size: 4.8rem;
    color: $brown-91;
    letter-spacing: getem(1000, 100);
    @include sp {
      font-size: 27px;
    }
  }

  &__action-text {
    font-size: 1.6rem;
    font-weight: $medium;
    @include sp {
      font-size: getsp375(26px);
    }
  }

  &__action-button {
    height: 47px;
    width: 251px;
    margin: 40px 0 0 -160px;
    @include sp {
      margin: 30px 0 20px 0;
    }
  }

  &__action-button-img {
    position: absolute;
    right: 40px;
    top: 50%;
    transform: translateY(-50%);
    transition: 0.3s ease-in;
  }

  &__action-button-link {
    border: 3px solid $grey-bc;
    border-radius: 23.5px;
    font-size: 1.8rem;
    font-weight: $bold;
    position: relative;
    white-space: nowrap;
    height: 47px;
    width: 251px;
    padding-right: 40px;
    @include center;

    &:hover .template3__action-button-img {
      right: 30px;
    }
  }

  &__action-image {
    position: absolute;
    right: 40px;
    top: 50%;
    transform: translateY(-50%);
    width: 225px;
    height: 235px;
    @include sp {
      position: relative;
      right: unset;
      top: unset;
      transform: none;
    }
  }

  &__content-01-container {
    @include max-width;
    margin: 100px auto 150px;
    @include mx1400 {
      padding: 0 3%;
    }
    @include sp {
      margin: 50px auto 75px;
    }
  }

  &__content-01-col-wrap {
    display: flex;
    justify-content: space-between;
    gap: 6.5%;
    @include sp {
      align-items: center;
      flex-direction: column;
      gap: 4%;
    }
  }

  &__content-01-image {
    width: 557px;
    height: 591.72px;
    @include sp {
      width: 100%;
      max-width: 557px;
      height: unset;
    }
  }

  &__content-01-text {
    width: 48%;
    max-width: 560px;
    font-size: 1.8rem;
    font-weight: $medium;
    line-height: getem(18, 36);
    @include sp {
      max-width: 100%;
      width: 100%;
      font-size: getsp375(28px);
    }

    p:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__content-01-title {
    font-size: 4.8rem;
    line-height: getem(48, 86);
    margin-bottom: 40px;
    @include sp {
      font-size: getsp375(58px);
      margin: 20px 0;
    }
  }

  &__content02 {
    background: $primary;
    padding: 0 3%;
    @include center;
  }

  &__content02-container {
    max-width: 1400px;
    width: 100%;
    @include sp {
      max-width: 100%;
    }
  }

  &__item {
    background: $white;
    border-radius: 100px;
    width: 100%;
    padding: 140px 100px;
    margin: 100px 0;
    @include mx1400 {
      padding: 100px 50px;
    }
    @include sp {
      padding: 30px 20px;
      margin: 50px 0;
      border-radius: 50px;
    }
  }

  &__item-col {
    display: flex;
    justify-content: start;
    gap: 6.5%;
    @include mx1400 {
      gap: 4%;
    }
    @include sp {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 4%;
    }

    &--mb-150 {
      margin-bottom: 150px;
      @include sp {
        margin-bottom: 75px;
      }
    }

    &--mb-80 {
      margin-bottom: 80px;
      @include sp {
        margin-bottom: 40px;
      }
    }

    &--mb-140 {
      margin-bottom: 140px;
      @include sp {
        margin-bottom: 70px;
      }
    }

    &--mw-1012 {
      max-width: 1012px;
      margin: 0 auto;
      @include sp {
        max-width: 100%;
      }
    }
  }

  &__item-title {
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    @include sp {
      margin-bottom: 25px;
    }

    &--m-70-negative15 {
      margin: 0 0 70px -15px;
      @include sp {
        margin: 0 0 35px 0;
      }
    }

    &--m-70-5 {
      margin: 0 0 70px 5px;
      @include sp {
        margin: 0 0 35px 0;
      }
    }

    &--m-70-negative5 {
      margin: 0 0 90px -5px;
      @include sp {
        margin: 0 0 45px 0;
      }
    }
  }

  &__item-number {
    font-size: 15.4rem;
    line-height: getem(154, 36);
    color: $brown-91;
    font-weight: $bold;
    font-family: $udshin;
    @include mx1400 {
      font-size: 14rem;
    }
    @include sp {
      font-size: getsp375(164px);
    }

    &--ml-negative10 {
      margin-left: -10px;
      @include sp {
        margin: 0;
      }
    }

    &--ml-negative8 {
      margin-left: -8px;
      @include sp {
        margin-left: 0;
      }
    }
  }

  &__item-subtitle {
    font-size: 4.2rem;
    line-height: getem(42, 68);
    color: $brown-91;
    padding-left: 30px;
    @include mx1400 {
      font-size: 3.8rem;
      padding-left: 20px;
    }
    @include sp {
      font-size: getsp375(52px);
      padding-left: 10px;
    }

    &--pl-15 {
      padding-left: 15px;
      @include sp {
        padding-left: 0;
      }
    }
  }

  &__item-subtext {
    font-size: 1.8rem;
    font-weight: $medium;
    line-height: getem(18, 36);
    padding-left: 45px;
    @include sp {
      font-size: getsp375(28px);
      padding-left: 0;
    }

    p:not(:last-child) {
      margin-bottom: 50px;
      @include sp {
        margin-bottom: 25px;
      }
    }

    &--pl-50 {
      padding-left: 50px;
      @include sp {
        padding-left: 0;
      }
    }

    &--pl-45 {
      padding-left: 45px;
      @include sp {
        padding-left: 0;
      }
    }

    &--p-mb-20 {
      p:not(:last-child) {
        margin-bottom: 20px;
        @include sp {
          margin-bottom: 10px;
        }
      }
    }

    &--pl-44 {
      padding-left: 44px;
      @include sp {
        padding-left: 0;
      }

      p:not(:last-child) {
        margin-bottom: 45px;
        @include sp {
          margin-bottom: 22.5px;
        }
      }
    }

    &--pl-39 {

      padding-left: 39px;
      @include sp {
        padding-left: 0;
      }

      p:not(:last-child) {
        margin-bottom: 20px;
        @include sp {
          margin-bottom: 10px;
        }
      }
    }
  }

  &__item-point {
    width: 560px;
    height: 425.53px;
    padding-top: 100px;
    border: 5px solid $blue-26;
    border-radius: 54.15px;
    position: relative;
    flex-direction: column;
    display: flex;
    align-items: center;
    @include sp {
      width: 100%;
      height: unset;
      border-radius: 27.07px;
      padding-top: 0;

      &:not(:last-child) {
        margin-bottom: 75px;
      }
    }
  }

  &__item-point-text {
    font-size: 2.4rem;
    font-weight: $medium;
    line-height: getem(24, 42);
    @include sp {
      font-size: getsp375(34px);
      margin: 75px 0 40px;
      padding: 0 20px;
    }
  }

  &__item-point-image {
    padding: 30px 10px 30px;
    width: 100%;
    height: 100%;
    @include sp {
      padding: 0 20px 20px;
    }
  }

  &__item-point-image-wrap {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;

    img {
      max-width: 100%;
    }
  }

  &__point {
    font-size: 3rem;
    line-height: getem(30, 61.38);
    position: absolute;
    top: -78.85px;
    left: 50%;
    transform: translateX(-50%);
    width: 157.70px;
    height: 157.70px;
    color: $white;
    background: $blue-26;
    border-radius: 50%;
    @include center;
    @include sp {
      font-size: getsp375(40px);
      width: 100px;
      height: 100px;
      top: -52.85px;
    }
  }

  &__item-text {
    max-width: 560px;
    width: 50%;
    @include sp {
      width: 100%;
      max-width: 100%;
    }
  }

  &__item-text-item6 {
    width: 60%;
    padding-top: 30px;
    @include sp {
      width: 100%;
    }
  }

  &__item1-img-wrap {
    width: 401px;
    height: 461px;
    @include sp {
      max-width: 401px;
      width: 100%;
      height: unset;
    }
  }

  &__item1-img {
    padding: 30px 0 0 40px;
    width: 50%;
    @include sp {
      width: 100%;
      max-width: 100%;
      padding-left: 0;
      @include center;
    }
  }

  &__item2-img-wrap {
    width: 462px;
    height: 525px;
    @include sp {
      width: 100%;
      height: unset;
      max-width: 462px;
    }
  }

  &__item2-img {
    width: 50%;
    padding-left: 70px;
    @include mx1400 {
      padding: 0;
    }
    @include sp {
      width: 100%;
      @include center;
    }
  }

  &__trivia {
    width: 100%;
    border: 5.58px solid $grey-bcb4;
    border-radius: 55.78px;
    @include sp {
      border-radius: 27.89px;
    }
  }

  &__trivia-title-wrap {
    font-size: 3.6rem;
    line-height: getem(36, 72);
    font-weight: $bold;
    color: $white;
    background: $grey-bcb4;
    text-align: center;
    height: 70.97px;
    width: calc(100% + 4px);
    margin: -2px 0 0 -2px;
    border-radius: 45.78px 49.78px 0 0;
    position: relative;
    @include sp {
      font-size: getsp375(46px);
      height: 50px;
      border-radius: 22.5px 24.5px 0 0;
    }
  }

  &__trivia-title {
    &::after,
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      height: 30px;
      width: 3px;
      background: white;
      @include sp {
        height: 25px;
      }
    }

    &::after {
      content: '';
      left: 50%;
      transform: translate(-50%, -50%) rotate(330deg);
      margin-left: -90px;
      @include sp {
        margin-left: -60px;
      }
    }

    &::before {
      content: '';
      right: 50%;
      transform: translate(-50%, -50%) rotate(30deg);
      margin-right: -90px;
      @include sp {
        margin-right: -60px;
      }
    }
  }

  &__trivia-subtitle {
    font-size: 2.6rem;
    line-height: getem(26, 72);
    text-align: center;
    @include sp {
      font-size: getsp375(36px);
      line-height: 1.5em;
      padding-bottom: 20px;
    }
  }

  &__trivia-text {
    padding: 0 80px 21px;
    margin: 20px 0 0 0;
    @include sp {
      padding: 0 20px 15px;
    }
  }

  &__trivia-subtext {
    font-size: 1.8rem;
    line-height: getem(18, 36);
    font-weight: $medium;
    @include sp {
      font-size: getsp375(28px);
    }
  }

  &__item3-img-wrap {
    width: 100%;
    max-width: 508px;
  }

  &__item3-img {
    padding-left: 28px;
    max-width: 560px;
    width: 50%;
    display: flex;
    align-items: flex-start;
    @include sp {
      width: 100%;
      padding: 0;
      margin-top: 30px;
    }
  }

  &__item4-img-wrap {
    width: 100%;
    max-width: 565px;
  }

  &__item4-img {
    width: 50%;
    display: flex;
    align-items: flex-start;
    @include sp {
      width: 100%;
      margin-bottom: 50px;
      align-items: center;
      justify-content: center;
    }
  }

  &__item5-img-wrap {
    width: 100%;
    @include sp {
      max-width: 532px;
    }
  }

  &__item5-img {
    max-width: 560px;
    width: 50%;
    padding-left: 30px;
    @include sp {
      width: 100%;
      padding: 0;
      margin-top: 20px;
    }
  }

  &__item6 {
    padding: 40px 100px 150px;
    width: 100%;
    @include sp {
      padding: 20px 0 75px;
    }
  }

  &__item6-title-wrap {
    margin-bottom: 80px;
    width: 801px;
    height: 86px;
    margin-inline: auto;
    position: relative;
    @include center;
    @include sp {
      width: 100%;
      height: unset;
      margin-bottom: 50px;
      background: white;
      border-radius: 15px;

      &::after {
        content: '';
        position: absolute;
        right: 40%;
        bottom: -40px;
        transform: translateX(-50%);
        border-top: 58px solid white;
        border-right: 15.25px solid transparent;
        border-left: 15.25px solid transparent;
        border-bottom: none;
        border-radius: 0;
      }
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      max-width: unset;
      max-height: unset;
    }
  }

  &__item6-title {
    font-size: 3.6rem;
    line-height: getem(36, 72);
    color: $brown-91;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    @include sp {
      font-size: getsp375(46px);
      padding: 10px 20px;
      position: relative;
      top: unset;
      transform: none;
    }
  }

  &__item6-img-wrap {
    width: 364px;
    height: 352px;
    @include sp {
      max-width: 364px;
      height: unset;
      width: 100%;
    }
  }

  &__item6-img {
    display: flex;
    width: 40%;
    justify-content: center;
    margin-left: -10px;
    @include sp {
      width: 100%;
      margin-left: 0;
      padding: 0 50px;
    }
  }

  &__inquiry-container {
    @include max-width;
    padding: 0 100px;
  }

  &__inquiry-title-wrap {
    margin: 150px 0 100px 0;
    text-align: center;
    position: relative;
    @include sp {
      margin: 75px 0 50px 0;
      font-size: getsp375(58px);
    }

    &::after {
      content: '';
      height: 7px;
      border-radius: 10px;
      background: $brown-d7;
      width: 91px;
      position: absolute;
      bottom: -40px;
      left: 50%;
      transform: translateX(-50%);
      @include sp {
        height: 4px;
        width: 50px;
        bottom: -20px;
      }
    }
  }

  &__inquiry-title {
    font-size: 4.8rem;
    line-height: getem(48, 26);
    letter-spacing: getem(1000, 50);
    @include sp {
      font-size: getsp375(58px);
    }
  }

  &__telephone-container {
    padding: 0 100px;
    height: 194px;
    width: 1000px;
    margin: 100px auto 100px;
    border-radius: 30px;
    box-shadow: rgb(0 0 0 / 10%) 0px 10px 20px;
    @include sp {
      width: 94%;
      height: unset;
      padding: 0;
      margin: 50px auto 50px;
    }
  }

  &__telephone-title {
    font-size: 2.8rem;
    font-weight: $bold;
    line-height: getem(28, 35);
    letter-spacing: getem(1000, 50);
    text-align: center;
    padding: 40px 0 0;
    @include sp {
      font-size: getsp375(38px);
      padding: 20px 0;
    }
  }

  &__telephone-text {
    @include center;
    margin: 20px 0;
    @include sp {
      flex-direction: column;
      margin: 0;
    }
  }

  &__telephone-text-number {
    @include center;
  }

  &__telephone-number {
    font-size: 4rem;
    font-weight: $bold;
    line-height: getem(40, 32);
    letter-spacing: getem(1000, 50);
    margin: 0 40px 0 20px;
    color: $blue-68;
    font-family: $novel;
    @include sp {
      font-size: getsp375(50px);
      margin: 10px 0;
    }
  }

  &__telephone-time {
    font-size: 1.6rem;
    font-weight: $regular;
    line-height: getem(16, 28);
    padding-top: 12px;
    @include sp {
      font-size: getsp375(26px);
      margin: 0 0 20px;
    }
  }

  &__telephone-img {
    width: 56px;
    height: 56px;
    @include sp {
      margin-right: 15px;
      width: 40px;
      height: 40px;
    }
  }

  &__copyright {
    height: 40px;
    width: 100%;
    font-size: 1.4rem;
    font-weight: $medium;
    line-height: getem(14, 36);
    letter-spacing: getem(1000, 50);
    background: $grey-f9;
    @include center;
    @include sp {
      font-size: 1rem;
      height: 30px;
    }
  }
}
