.form {
  &__item {
    display: flex;
    align-items: flex-start;
    gap: 27px;
    margin-bottom: 40px;
    @include sp {
      gap: 20px;
      flex-direction: column;
      margin-bottom: 20px;
    }
  }

  &__label-wrap {
    flex-basis: 240px;
    flex-shrink: 0;
    display: flex;
    padding-top: 12px;
    align-items: center;
    @include sp {
      flex-basis: 100%;
      justify-content: space-between;
      width: 100%;
    }

    &--checkbox {
      font-size: 2rem;
      font-weight: $bold;
      line-height: getem(20, 32);
      letter-spacing: getem(1000, 50);
      justify-content: center;
      @include sp {
        font-size: getsp375(30px);
      }
    }

    &--required::after {
      content: '必須';
      display: block;
      width: 49px;
      height: 24px;
      border-radius: 5px;
      background-color: $brown-d7;
      font-size: 1.6rem;
      font-weight: $bold;
      color: $white;
      letter-spacing: getem(1000, 50);
      flex-shrink: 0;
      margin-left: 17px;
      @include center;
      @include sp {
        font-size: 14px;
      }
    }
  }

  &__label {
    font-size: 2rem;
    font-weight: $bold;
    line-height: getem(20, 32);
    letter-spacing: getem(1000, 50);
    @include sp {
      font-size: getsp375(30px);
    }
  }

  &__input-wrap {
    width: 100%;

    input,
    textarea {
      font-size: 2rem;
      font-weight: $bold;
      font-family: $yugo;
      line-height: getem(20, 32);
      letter-spacing: getem(1000, 50);
      width: 100%;
      height: 59.5px;
      padding: 10px 40px;
      border-radius: 15px;
      border: 1px solid $blue-68;
      @include sp {
        font-size: 1.6rem;
        border-radius: 8px;
        padding: 10px 20px;
      }
    }

    textarea {
      height: 340.5px;
    }
  }

  input[type='checkbox'] {
    display: none;

    &:not(:checked) + span,
    &:checked + span {
      position: relative;
      display: block;
      width: 30px;
      height: 30px;
      margin-right: 20px;
      border: 1px solid $blue-68;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      @include sp {
        width: 25px;
        height: 25px;
        border-radius: 8px;
      }

      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        margin: auto;
        display: block;
        width: 100%;
        height: 100%;
        background: url('/img/icon/check-bold.svg');
        background-repeat: no-repeat;
        background-position: center;
        transition: opacity 250ms ease;
        opacity: 0;
      }
    }

    &:checked + span {
      &::after {
        opacity: 1;
      }
    }
  }

  &__checkbox {
    display: flex;
    cursor: pointer;
    margin-left: -10px;
  }

  &__button-wrap {
    @include center;
    margin-top: 35px;
    @include sp {
      margin-top: 20px;
    }
  }

  &__button {
    height: 60px;
    width: 294.5px;
    background-color: $blue-68;
    border-radius: 15px;
    color: $white;
    font-size: 2rem;
    font-weight: $bold;
    line-height: getem(20, 32);
    letter-spacing: getem(1000, 50);
    font-family: $yugo;
    border: none;
    cursor: pointer;
    transition: 0.3s ease-in;
    @include sp {
      font-size: getsp375(30px);
      height: 45px;
      border-radius: 8px;
    }

    &:hover {
      opacity: 0.9;
    }
  }
}
